// Colors
$primary-color: #1c3b4f;
$main-secondary-color: #f58439;
$secondary-color: #ffc289;
$tertiary-color: #ffffff;
$bg-color: #1a1e23;
$html: #e54f26;
$css: #0c73b8;
$js: #e7a020;
$react: #28a9e0;
$nodejs: #68a063;
$sass: #cd6799;
$mongoDB: #ffffff;
$bootstrap: #602c50;

//Les différents breack-point (390px - 768px - 1280px - 1920px)

@mixin globalPaddingLeftRight {
    padding-left: 24px;
    padding-right: 24px;
    @media screen and (min-width: 1280px) {
        padding-left: 128px;
        padding-right: 128px;
    }
}

@mixin globalPaddingTopBottom {
    padding-top: 64px;
    padding-bottom: 64px;
    @media screen and (min-width: 1920px) {
        padding-top: 128px;
        padding-bottom: 128px;
    }
}

@mixin globalMarginBottom {
    margin-bottom: 64px;
}

@mixin marginBottomResponsive {
    margin-bottom: 128px;
    @media screen and (max-width: 786px) {
        margin-bottom: 64px;
    }
}

@mixin paraPaddingLeft {
    padding-left: 30px;
    @media screen and (max-width: 390px) {
        padding-left: 0;
    }
}

@mixin backgroundSection {
    width: 100vw;
    position: absolute;
    height: 100%;
    opacity: 0.4;
}

@mixin specifiqueTitileSection($border-color, $text-color) {
    font-size: 64px;
    padding: 16px 40px;
    border-radius: 40px 0px;
    border: 4px solid $border-color;
    background-color: $primary-color;
    color: $text-color;
    @media screen and (max-width: 545px) {
        font-size: 32px;
    }
}

@mixin specialLink {
    position: relative;
    font-weight: 500;
    text-decoration: underline;
    text-decoration-color: $tertiary-color;
    text-decoration-thickness: 2px; /* Épaisseur de l'underline */
    text-underline-offset: 10px; /* Espacement entre le texte et l'underline */
    @media screen and (max-width: 460px) {
        font-size: 16px;
    }
    @media screen and (max-width: 350px) {
        font-size: 12px;
    }
}
